.card{
    box-shadow:0 0 20px rgba(0,0,0,0.2);
    padding:1rem 1rem ;
    border-radius: 15px;
    background: rgba(241, 237, 237, 0.5);
    max-width: 300px;
    height:60vh;
    overflow:auto;
    position: absolute;
    top:25%;
    left:10px;
    z-index: 5;
}
.card .welcome h3{
    font-weight:bold;
    padding:0.5rem 0 0.5rem 0;
}
.introcont p{
    padding:1rem 0 0 0;
    text-align:justify;
    text-justify: inter-word;
}
.NewsImagecontainer{
    width:100%;
    height:50vh;
}
.NICC{
    display:flex;
    justify-content:space-between;
    padding:1rem 0 0 1rem;
    border-radius:15px;
    padding:0.5 1rem;
}

