.header{
    display:flex;
    justify-content:space-between;
    align-items: center;
    padding: 1rem;
    position:fixed;
    width:100%;
    height:50px;
    z-index:10;
}
.header-bg{
    background-color:rgb(255, 255, 255);
    transition: 0.5s;
}
