.nav-menu{
    display:flex;
}
.nav-menu li{
    padding:0 1rem;
}
.nav-menu li a{
    font-size:1.2rem;
    font-weight:500;
}
.hamburger{
    display:none;
}

@media screen and (max-width: 1040px){
    .nav-menu{
        flex-direction:column;
        justify-content:center;
        align-items:center;
        width:100%;
        height:100vh;
        position: absolute;
        top:0;
        left:-100%;
        background-color:aliceblue;
        opacity: 0.8;
        z-index:-3;
        transition:0.3s;
    }
    .nav-menu.active{
        left:0;
    }
    .nav-menu li{
        padding:1rem 0;
    }
    .nav-menu li a{
        font-size:2rem;
    }
    .hamburger{
        display:initial;
    }

}