.concard{
    box-shadow:0 0 20px rgba(0,0,0,0.2);
    padding:1rem 1rem ;
    border-radius: 15px;
    background: rgba(241, 237, 237, 0.5);
    width: 500px;
    height:60vh;
    overflow:auto;
    position: absolute;
    top:25%;
    left:32%;
    right:33.33%;
    z-index: 5;
}
.concard{
    justify-content: center;
    
}
.concard .welcome h3{
    font-weight:bold;
    font-size: large;
    padding:0.5rem 0 0.5rem 12rem;
    
}
.conintrocont {
    padding:5rem 0 0 0;
    text-align:justify;
    text-justify: inter-word;
}
.conline1{
    display:flex;
    justify-content:center;
    height:20px;
}
