.rehero .retitle{
    padding:0rem 1rem 1rem 1rem;
}

.recont{
    width:100%;
    display:grid;
    grid-template-columns:repeat(1,1fr);
    grid-gap:40px;
    padding:1rem 1rem 3rem 1rem;
}

.recardcont{
    display:flex;
    justify-content:space-between;
    padding: 1rem 1rem;
    width:100%;
    /* height:30vh; */
    overflow:auto;
    /* box-shadow:0 0 20px rgba(0,0,0,0.2); */
    border-radius: 15px;
}
.releft img{
    width:200px;
    padding:2rem 1rem 0 0;
}

.reright p{
    padding:0.3rem 0;
    text-align:justify;
    font-size:medium;
}

.reright h3{
    padding:0 0 0 0;
}
