.pplhero .ppltitle{
    padding:0rem 1rem 1rem 1rem;
}

.pplimgcont{
    width:100%;
    display:grid;
    grid-template-columns:repeat(2, 1fr);
    grid-gap:40px;
    padding-bottom: 5rem;
    padding-left: 1rem;
}

.pplimgcardcont{
    display: flex;
    justify-content: space-between;
    padding: 1rem 1rem;
    width:100%;
    height:40vh;
    overflow:auto;
    box-shadow:0 0 20px rgba(0,0,0,0.2);
    border-radius: 15px;
}
.pplleft{
    width:150px;
    height:300px;
    padding:1rem 1rem 0 0;
}
.pplavatar{
    width:100%;
}
.pplright p{
    text-align: justify;
}
.ppliconscont{
    width: 100%;
    padding-left: 85%;
}